2003年接手系列剧时，她将Huntress添加到阵容中。在她的第一个名为“Of Like Minds”的曲目中，Simone让黑色金丝雀走进了被称为Savant的Brian Durlin 和他的助手Creote设置的陷阱。由于黑色金丝雀现在受到严重伤害和束缚，萨凡特开始列出要求，其中最重要的是蝙蝠侠的真实身份。最后，女猎手和金丝雀击败他们的敌人，并组成一个团队。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br'),_c('h2',[_vm._v("影视作品及游戏")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"})])}
